.wrapper {
  min-height: 100%;
  background-color: #303640;
  justify-content: center;
  align-items: center;
  padding: 50px;
  display: flex;
  overflow: clip;
}

.menu {
  background-color: #303640;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}

.menu__list {
  gap: 10px;
  padding: 30px 0;
  display: grid;
}

.menu__item {
  color: #a6aab4;
  transition: color .8s;
  position: relative;
  overflow: hidden;
}

.menu__item :hover, .menu__item ._active {
  color: #fff;
}

.menu__link {
  text-transform: uppercase;
  color: currentColor;
  padding: 20px 80px 20px 30px;
  font-weight: 700;
  text-decoration: none;
  display: block;
}

.menu__active {
  width: 8px;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.menu__active span {
  width: 100%;
  height: 100%;
  border-radius: 0 5px 5px 0;
  transition: all .5s;
  position: absolute;
  top: 0;
  left: 0;
}

.menu {
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 15px rgba(0, 0, 0, 0);
}

.loaded .menu {
  border: 1px solid rgba(255, 255, 255, .05);
  transition: all 1s 1.5s;
  box-shadow: inset 0 0 15px rgba(0, 0, 0, .25);
}

.menu__link {
  transform: translate(-100%);
}

.loaded .menu__link {
  transition: transform 1s;
  transform: translate(0);
}

.loaded .menu__item:nth-child(1) .menu__link {
  transition-delay: .2s;
}

.loaded .menu__item:nth-child(2) .menu__link {
  transition-delay: .4s;
}

.loaded .menu__item:nth-child(3) .menu__link {
  transition-delay: .6s;
}

.loaded .menu__item:nth-child(4) .menu__link {
  transition-delay: .8s;
}

.menu__active {
  transform: translate(-100%);
}

.loaded .menu__active {
  transition: transform .5s 2s;
  transform: translate(0);
}

